<template>
  <div class="my-page-container">
    <!-- 顶部个人信息卡片 -->
    <div class="user-card">
      <div class="avatar-container">
        <img src="../assets/avatar.png" alt="头像" class="avatar-img" />
      </div>
      <div class="user-info">
        <div class="user-name-container">
          <span class="user-name">李爷爷</span>
          <div class="verified-badge">
            <img src="../assets/verified-icon.png" alt="已认证" class="verified-icon" />
            <span class="verified-text">已认证</span>
          </div>
        </div>
        <div class="user-id">431 xxxxxxxxxxxx</div>
      </div>
    </div>

    <!-- 功能按钮区域 -->
    <div class="function-grid">
      <div class="function-row">
        <div class="function-item" @click="goToEmergencyContacts()">
          <div class="function-icon emergency-contact">
            <img src="../assets/emergency-contact-icon.png" alt="紧急联系人" />
          </div>
          <div class="function-name">紧急联系人</div>
        </div>
        <div class="function-item" @click="goToInsuranceCode()">
          <div class="function-icon qr-code">
            <img src="../assets/qr-code-icon.png" alt="查看医保码" />
          </div>
          <div class="function-name">查看医保码</div>
        </div>
      </div>
      <div class="function-row">
        <div class="function-item" @click="goToBillDetail()">
          <div class="function-icon bill">
            <img src="../assets/bill-icon.png" alt="看账单" />
          </div>
          <div class="function-name">看账单</div>
        </div>
        <div class="function-item" @click="goToMedicalRecord()">
          <div class="function-icon medical-record">
            <img src="../assets/medical-record-icon.png" alt="就诊记录" />
          </div>
          <div class="function-name">就诊记录</div>
        </div>
        <div class="function-item" @click="goToReportPage()">
          <div class="function-icon report">
            <img src="../assets/report-icon1.png" alt="查报告" />
          </div>
          <div class="function-name">查报告</div>
        </div>
      </div>
    </div>

    <!-- 设置选项列表 -->
    <div class="settings-list">
      <div class="settings-item" @click="goToEmergencyCall()">
        <div class="settings-icon emergency-call">
          <img src="../assets/emergency-call-icon.png" alt="紧急呼叫" />
        </div>
        <div class="settings-name">紧急呼叫</div>
        <div class="settings-arrow">
          <img src="../assets/arrow-right.png" alt=">" />
        </div>
      </div>
      <div class="settings-item" @click="goToSpeechRate()">
        <div class="settings-icon speech-rate">
          <img src="../assets/speech-rate-icon.png" alt="语速调节" />
        </div>
        <div class="settings-name">语速调节</div>
        <div class="settings-arrow">
          <img src="../assets/arrow-right.png" alt=">" />
        </div>
      </div>
      <div class="settings-item" @click="goToFontSize()">
        <div class="settings-icon font-size">
          <img src="../assets/font-size-icon.png" alt="字体大小" />
        </div>
        <div class="settings-name">字体大小</div>
        <div class="settings-arrow">
          <img src="../assets/arrow-right.png" alt=">" />
        </div>
      </div>
    </div>

    <!-- 底部导航栏 -->
    <div class="bottom-nav">
      <div class="nav-item" @click="goToHomePage">
        <div class="nav-icon">
          <img src="../assets/home-icon1.png" alt="首页" class="nav-icon-img" />
        </div>
        <div class="nav-text">首页</div>
      </div>
      <div class="nav-item active">
        <div class="nav-icon">
          <img src="../assets/my-icon1.png" alt="我的" class="nav-icon-img" />
        </div>
        <div class="nav-text">我的</div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'MyPage',
  data() {
    return {
      fromReportView: false
    }
  },
  created() {
    // 获取来源信息
    this.fromReportView = this.$route.query.fromReportView === 'true';
    console.log('MyPage created, fromReportView:', this.fromReportView);
  },
  methods: {
    goToHomePage() {
      // 根据来源返回相应的首页状态
      this.$router.push({
        path: '/home',
        query: { 
          showReportPage: this.fromReportView ? 'true' : 'false'
        }
      });
      console.log('从我的页面返回首页，目标状态:', this.fromReportView ? '查报告' : '去挂号');
    },
    
    goToEmergencyContacts() {
      // 跳转到紧急联系人页面
      this.$router.push({
        path: '/emergency-contacts',
        query: { 
          fromReportView: this.fromReportView ? 'true' : 'false'
        }
      });
    },
    
    goToInsuranceCode() {
      // 跳转到医保码页面
      this.$router.push({
        path: '/insurance-code',
        query: { 
          fromReportView: this.fromReportView ? 'true' : 'false'
        }
      });
    },
    
    goToBillDetail() {
      // 跳转到账单明细页面
      this.$router.push({
        path: '/bill-detail',
        query: { 
          fromReportView: this.fromReportView ? 'true' : 'false'
        }
      });
    },
    
    goToEmergencyCall() {
      // 跳转到紧急呼救页面
      this.$router.push({
        path: '/emergency-call',
        query: { 
          fromReportView: this.fromReportView ? 'true' : 'false'
        }
      });
    },
    
    goToMedicalRecord() {
      // 跳转到就诊记录页面
      this.$router.push({
        path: '/medical-record',
        query: { 
          fromReportView: this.fromReportView ? 'true' : 'false'
        }
      });
    },
    
    goToSpeechRate() {
      // 跳转到语速调节页面
      this.$router.push({
        path: '/speech-rate',
        query: { 
          fromReportView: this.fromReportView ? 'true' : 'false'
        }
      });
    },
    
    goToFontSize() {
      // 跳转到字体大小设置页面
      this.$router.push({
        path: '/font-size',
        query: { 
          fromReportView: this.fromReportView ? 'true' : 'false'
        }
      });
    },
    
    goToReportPage() {
      // 跳转到报告页面
      this.$router.push({
        path: '/report-page',
        query: { 
          fromReportView: this.fromReportView ? 'true' : 'false'
        }
      });
    }
  }
}
</script>

